/* .content */
.post-tag {
  display: inline-block;
  min-width: 2rem;
  text-align: center;
  border-radius: 0.5rem;
  border: 1px solid var(--btn-border-color);
  padding: 0 0.4rem;
  color: var(--text-muted-color);
  line-height: 1.3rem;
}
