  margin-right: 0.2rem;
}

.rounded-10 {
  border-radius: 10px !important;
}

.img-link {
  color: transparent;
  display: inline-flex;
}

.shimmer {
  overflow: hidden;
  position: relative;
  background: var(--img-bg);
}
.shimmer::before {
  content: "";
  position: absolute;
  background: var(--shimmer-bg);
  height: 100%;
  width: 100%;
  -webkit-animation: shimmer 1.3s infinite;
  animation: shimmer 1.3s infinite;
}
@-webkit-keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
