  padding: 0.4rem 0 0.4rem 1.25rem;
}
#toc-wrapper ul {
  /* Overwrite TOC plugin style */
}
#toc-wrapper ul .toc-link {
  display: block;
}
#toc-wrapper ul .toc-link:hover {
  color: var(--toc-highlight);
  text-decoration: none;
}
#toc-wrapper ul .toc-link::before {
  display: none;
}
#toc-wrapper ul .is-active-link {
  color: var(--toc-highlight) !important;
  font-weight: 600;
}
#toc-wrapper ul .is-active-link::before {
  display: inline-block;
  width: 1px;
  height: 1.25rem;
  background-color: var(--toc-highlight) !important;
}
#toc-wrapper ul ul {
  padding-left: 0.75rem;
}
.toc-border-cover {
  margin-bottom: -4rem;
}

#toc-wrapper::after {
  content: "";
  position: fixed;
  bottom: 0;
  width: 15%;
  height: 2.25rem;
  margin-left: -1px;
  background: linear-gradient(transparent, var(--main-bg) 70%);
}
/* --- TOC button, bar and popup in mobile/tablet --- */
#toc-bar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  margin: 0 -1rem;
  height: 3rem;
  background: var(--main-bg);
  border-bottom: 1px solid var(--main-border-color);
  transition: all 0.2s ease-in-out;
}
@media all and (min-width: 1200px) {
  #toc-bar {
    display: none !important;
  }
}
#toc-bar .label {
  margin-left: 0.375rem;
  padding: 0 0.75rem;
  color: inherit;
}
#toc-bar.invisible {
  top: -3rem;
  transition: none;
}

#toc-solo-trigger {
  color: var(--text-muted-color);
  border-color: var(--btn-border-color);
  border-radius: 10px;
}
@media all and (min-width: 1200px) {
  #toc-solo-trigger {
    display: none !important;
  }
}
#toc-solo-trigger .label {
  font-size: 1rem;
  font-family: Lato, "Microsoft Yahei", sans-serif;
}
#toc-solo-trigger:hover {
  box-shadow: none;
  background: none;
}

@-webkit-keyframes slide-in {
  from {
    opacity: 0.7;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slide-in {
  from {
    opacity: 0.7;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@-webkit-keyframes slide-out {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-3rem);
    opacity: 0;
  }
}
@keyframes slide-out {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-3rem);
    opacity: 0;
  }
}
